
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import Clipboard from 'clipboard'
import PasswordConfirm from '../components/PasswordConfirm.vue'

export default {
components: {
XButton,
XHeader,
Group,
Cell,
Flexbox,
FlexboxItem,
PasswordConfirm
},
data () {
return {
wifKey: '',
keyCopied: false,
unlocked: false,
isUnlock: false,
wallet: {},
error: {
common: ''
}
}
},
methods: {
unlock () {
this.error.common = ''
this.isUnlock = true
},
unlocking (pwd) {
let self = this
if (!pwd.trim()) {
this.error.common = this.$t('unlock.error.invalid_password')
return
}
let account = this.$route.query.account
let wallets = this.$common.get_wallets()
let wallet = wallets.find(w => {
return w.account === account
})
console.log(wallet)
let publicWallet = this.$common.backupPublicKey(wallet.active, pwd)
let activePubkey = wallet.active_pubkey
if (wallet == null) {
self.isUnlock = false
self.error.common = self.$t('unlock.account_not_found')
} else if (activePubkey !== publicWallet) {
self.isUnlock = false
self.error.common = self.$t(
'wallet_backup.detail.error.invalid_password'
)
} else {
let data = {
account: wallet.account,
active: wallet.active,
owner: wallet.owner
}
let wifKey = this.$common.backupExport(data, pwd)
self.wifKey = wifKey
self.wallet = wallet
self.isUnlock = false
}
},
copyKey () {
let clipboard = new Clipboard('#copy')
clipboard.on('success', e => {
this.keyCopied = true
setTimeout(() => {
this.keyCopied = false
}, 1000)

clipboard.destroy()
})
clipboard.on('error', e => {

console.log('该浏览器不支持自动复制')

clipboard.destroy()
})
},
setUnlock (val) {
this.isUnlock = val
}
},
mounted () {
}
}



