
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import AccountImage from '../components/AccountImage'

export default {
components: {
XHeader,
Group,
Cell,
Flexbox,
FlexboxItem,
AccountImage
},
data () {
let wallets = this.$common.get_wallets()
return {
wallets: wallets,
balanceArr: []
}
},
methods: {
loadWallets () {
if (this.wallets.length === 0) {
this.$router.replace({
path: this.link('/wallet-create')
})
} else {
this.wallets.forEach((wallet, index) => {
wallet.balance = '0'
this.loadBalance(wallet, index)
})
}
},
loadBalance (wallet, index) {
if (wallet.account) {
let account = wallet.account
this.$http.get(`chain/accounts/eos/${account}`).then(res => {
let data = res.data
if (data.code === 200) {
this.balanceArr[index] = data.data.eos_balance.split(' ')[0]
} else {
this.balanceArr[index] = '0'
}
})
}
},
goDetail (account) {
this.$router.push({path: '/wallet-backup', query: {account: account}})
}
},
mounted () {
this.loadWallets()
}
}



