
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
components: {
XInput,
XButton
},
data () {
return {
password: '',
submitting: false,
error: {
common: ''
}
}
},
props: {
tips: {
type: String
}
},
methods: {
onEnter () {
this.onPasswordConfirm()
},
onPasswordConfirm () {
this.$emit('unlocking', this.password)
},
hidden () {
this.$emit('setUnlock', false)
}
}
}



